                        </v-flex>

                        <v-flex xs6 md2> <!-- Change profile picture here -->
                            <v-upload-button title="Change Picture" color="green lighten-3 white--text" depressed>

                            </v-upload-button>
                        </v-flex>

                        <v-flex xs12> <!-- Vertication Badges The User has obtained -->

                            <v-subheader>Badges</v-subheader>

                        </v-flex>

                    </v-layout>

                </v-flex>

                <v-flex xs0 md2></v-flex>

                <v-flex xs12 md7> <!-- Right side of the screen that includes: Previous Post, User Bio, and password change -->

                    <v-layout align-start column>

                        <v-flex xs12> <!-- Container that holds all the posted content -->

                            <v-list> <!-- List container that holds post -->

                                <v-subheader class="headline">Your Comments</v-subheader>
                                
                                <template v-for="posts in userPost">

                                    <v-card :key="posts.date" flat>

                                        <v-layout justify-center align-center wrap> <!-- Everything in the card is in a layout so stuff is aligned horizonally -->

