                                                    {{posts.message}}
                                                </v-card-text>

                                            </v-flex>

                                            <v-flex xs12> <!-- Date & Delete Option -->
                                                <v-layout align-end justify-end>
                                                    <v-flex xs2 md1>
                                                        <span @click="someFunction()" class="font-weight-bold red--text text--lighten-2">Delete</span>
                                                    </v-flex>

                                                    <v-flex xs2 md2>
                                                        <span class="font-weight-bold">{{posts.date}}</span>
                                                    </v-flex>
                                                </v-layout>
                                            </v-flex>

                                        </v-layout>

                                    </v-card>
                                    
                                </template>

                            </v-list>

                        </v-flex>

                        <!-- <v-flex xs12>

                            <v-list>
                                <v-subheader>
                                    Bio
                                </v-subheader>

                                <v-text-area placeholder="Add a Bio Message Here...">
                                </v-text-area>
                            </v-list>

                        </v-flex > -->

