</template>

<script>
import {auth,database,storage} from '@/fb.js';
import UploadButton from 'vuetify-upload-button';
const storageReference = storage.ref();

export default {
    created() {
    },
    components: {
        'v-upload-button': UploadButton
    },
    data() {
        return {
            userProfilePicture: auth.currentUser.photoURL,
            username: auth.currentUser.displayName,
            email: auth.currentUser.email,
            userPost: [
                {message: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin sed iaculis dolor, blandit faucibus orci. Proin gravida nec nunc sed mattis. Donec a tempor libero. Sed vel turpis feugiat, suscipit sem nec, posuere orci. Integer eleifend urna sed quam mollis, quis maximus magna mollis. Integer finibus venenatis lectus sit amet rutrum. Morbi pharetra congue risus at dictum. Sed sit amet sapien lorem. Aliquam non condimentum enim. Nulla dapibus ultricies tempus. Donec laoreet sed sem vitae ullamcorper.',date: '19/04/20'},
                {message: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Proin sed iaculis dolor, blandit faucibus orci. Proin gravida nec nunc sed mattis. Donec a tempor libero. Sed vel turpis feugiat, suscipit sem nec, posuere orci. Integer eleifend urna sed quam mollis, quis maximus magna mollis. Integer finibus venenatis lectus sit amet rutrum. Morbi pharetra congue risus at dictum. Sed sit amet sapien lorem. Aliquam non condimentum enim. Nulla dapibus ultricies tempus. Donec laoreet sed sem vitae ullamcorper.',date: '19/05/20'},
            ]
        }
    },
    methods: {
        someFunction: function() {
            console.log('How do you feel?');
        },
        fileHander(file) {
            if(file.type === "image/png" || file.type === "image/jpeg") {
                console.log('OOGA BOOGA!');
                this.changeImage(file);
            } else {
                this.acualImage = null;
                this.curImage = '';
            }
        },
        changeImage(file) {
            console.log('updating user picture successful');
            let userCode = `userData/${auth.currentUser.uid}/userImage`;

                storageReference.child(userCode).put(file).then(() => { //updates the photo in storage
                    
                    storageReference.child(userCode).getDownloadURL().then(url => { //gets download url for photo and updates for users avatar

                        auth.currentUser.updateProfile({
                            photoURL: url
                        })
                    });
                })
        }
    }
}
</script>